import Link from "next/link";
import Image from "next/image";
import React from "react";

const grades = [
  { title: "Grade 6", image: "/images/grades/grade-6.jpg", href: "/grades/6" },
  { title: "Grade 7", image: "/images/grades/grade-7.jpg", href: "/grades/7" },
  { title: "Grade 8", image: "/images/grades/grade-8.jpg", href: "/grades/8" },
  { title: "Grade 9", image: "/images/grades/grade-9.jpg", href: "/grades/9" },
  { title: "Grade 10", image: "/images/grades/grade-10.jpg", href: "/grades/10" },
  { title: "Grade 11", image: "/images/grades/grade-11.jpg", href: "/grades/11" },
  { title: "A/L ICT", image: "/images/grades/al-ict.jpg", href: "/grades/al-ict" },
];

const MainCategoryHero = () => {
  return (
<section className="py-20 bg-gray-50">
  
    <SectionHeading
          title="Your Learning Journey"
          subtitle="Master high-demand skills and ace your exams with our proven process"
          align="center"
        />

    <div className="grid gap-8 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4">
      {grades.map((grade) => (
        <Link
          key={grade.title}
          href={grade.href}
          className="group rounded-2xl overflow-hidden bg-white shadow-md hover:shadow-xl transition"
        >
          <div className="h-44 overflow-hidden">
            <Image
              src={grade.image}
              alt={grade.title}
              width={400}
              height={176}
              className="h-full w-full object-cover group-hover:scale-105 transition-transform duration-300"
            />
          </div>
          <div className="p-5 text-center">
            <h2 className="text-xl font-semibold">{grade.title}</h2>
          </div>
        </Link>
      ))}
    </div>
  
</section>


  );
};

export default MainCategoryHero;
